import { Component, OnDestroy, OnInit, ViewEncapsulation } from '@angular/core';
import { ActivatedRoute, Data, Params, Router } from '@angular/router';
import { TranslateService } from '@ngx-translate/core';
import { SortDescriptor } from '@progress/kendo-data-query';
import { NgxSmartModalService } from 'ngx-smart-modal';
import { ToastrService } from 'ngx-toastr';
import { Subscription, combineLatest } from 'rxjs';
import {
  DataState,
  FieldDefinition,
  ICombinedColumn,
  MetaDataType,
  ObjectIdGenerator,
  TableItem,
  UserPreferencesService,
} from 'shared-code-lib';
import { Log } from '../../blocks/core/log';
import { DBColumn, DataSource, IDBColumn, IDataSource, IDataSourceRow } from '../../blocks/models/datasource';
import { ErrorsService } from '../../blocks/services/errors.service';
import { HydraApiService } from '../../blocks/services/hydra-api.service';
import { ApiResult, Control } from '../../blocks/services/models';
import { SpinnerStateService } from '../../blocks/services/spinner-state.service';

@Component({
  selector: 'app-admin-datasource',
  templateUrl: './admin-datasource.component.html',
  styleUrls: ['./admin-datasource.component.scss'],
  encapsulation: ViewEncapsulation.None,
})
export class AdminDataSourceComponent implements OnInit, OnDestroy {
  rightControls: Array<Control> = [];

  routeParams: Params;
  routeData: Data;

  datasourceName: FieldDefinition;
  newColumnName: FieldDefinition;
  newColumnNameValue: string;

  saveButton: Control;
  cancelButton: Control;
  datasource: DataSource;

  rowsSort: SortDescriptor = { field: '1', dir: 'desc' };
  items: TableItem[] = [];
  columns: ICombinedColumn[];
  selectedRow: IDataSourceRow;
  rowModalIsVisible: boolean = false;

  newColumnFieldFocused: boolean;

  currentPage = 1;
  itemsTotal = 0;
  pageSize = 25;

  subscriptions = new Subscription();

  constructor(
    private translateService: TranslateService,
    private router: Router,
    private route: ActivatedRoute,
    private hydraApi: HydraApiService,
    private toastr: ToastrService,
    private errorsService: ErrorsService,
    private spinner: SpinnerStateService,
    private userPreferencesService: UserPreferencesService,
    public ngxSmartModalService: NgxSmartModalService
  ) {}

  ngOnInit() {
    this.createFields();

    this.subscriptions.add(
      combineLatest([this.route.params, this.route.data]).subscribe(([params, data]) => {
        this.routeParams = params;
        this.routeData = data;
        if (this.routeData.type == 'create') {
          const newDataSource = {
            name: '',
            columns: [],
            dateCreatedUTC: new Date(),
            dateModifiedUTC: new Date(),
          } as IDataSource;
          this.datasource = new DataSource(newDataSource);
        } else {
          this.loadItems();
        }
      })
    );
  }

  private createFields = () => {
    this.saveButton = new Control(this.onSave, 'Save', 'btn btn-primary btn-minwidth', '', '', 'btn', false);
    this.cancelButton = new Control(this.onCancel, 'Cancel', 'btn btn-light btn-minwidth', '', '', 'btn', false);
    this.rightControls.push(this.saveButton);
    this.rightControls.push(this.cancelButton);

    this.translateService.get('Common.Save').subscribe((res) => {
      this.saveButton.setTranslation(res);
    });
    this.translateService.get('Common.Cancel').subscribe((res) => {
      this.cancelButton.setTranslation(res);
    });

    this.datasourceName = new FieldDefinition({
      type: MetaDataType.Text,
      label: this.translateService.instant('admin-datasource.name'),
      placeholder: this.translateService.instant('admin-datasource.name'),
      required: true,
    });

    this.newColumnName = new FieldDefinition({
      type: MetaDataType.Text,
      label: this.translateService.instant('admin-datasource.column-name'),
      placeholder: this.translateService.instant('admin-datasource.column-name'),
      required: true,
    });
  };

  onSave = () => {
    if (this.newColumnNameValue) {
      this.errorsService.showError(this.translateService.instant('admin-datasource.pending-column-error-message'), '');
      return;
    }

    if (this.datasource.id) {
      this.hydraApi.updateDataSource(this.datasource, this.datasourceSaved, this.datasourceSaveFailed);
    } else {
      this.hydraApi.createDataSource(this.datasource, this.datasourceSaved, this.datasourceSaveFailed);
    }

    this.spinner.showSavingSpinner();
  };

  datasourceSaved = (result: ApiResult) => {
    if (result.success) {
      this.toastr.success(this.translateService.instant('admin-datasource.saved'));
      this.returnToGrid();
    } else {
      this.errorsService.showErrorsModal(result.errors, 'admin-datasource.validation-errors');
    }
    this.spinner.hide();
  };

  datasourceSaveFailed = (error: any) => {
    this.toastr.error(this.translateService.instant('admin-datasource.save-failed'));
    Log.error('admin-datasource.save-failed', error);
    this.spinner.hide();
  };

  onCancel = () => {
    this.returnToGrid();
  };

  returnToGrid = () => {
    this.router.navigateByUrl(
      'settings/BambaSettingsApplication/BambaSettingsApplication-Upland.Bamba.Application.Settings.DataSources'
    );
  };

  datasourceLoaded = (ds: any): void => {
    this.datasource = ds;
    this.itemsTotal = ds.totalRowsCount;
    this.spinner.hide();
  };

  datasourceFailed = (error: any): void => {
    this.toastr.error(this.translateService.instant('admin-datasource.load-failed'));
    Log.error('admin-datasource.load-failed', error);
    this.spinner.hide();
  };

  loadItems = (): void => {
    if (this.routeData.type === 'properties') {
      this.spinner.show(this.translateService.instant('admin-datasource.loading-datasource'), document.body);
      this.hydraApi.getDataSource(this.routeParams.datasourceid, this.datasourceLoaded, this.datasourceFailed);
    }
  };

  onPageSizeChanged = (pageSize: number): void => {
    if (this.routeParams.datasourceid)
      this.userPreferencesService.setPreference(`folder-pagesize-${this.routeParams.datasourceid}`, pageSize);
  };

  onAddColumn = (): void => {
    if (!this.datasource) return;
    if (!this.datasource.columns) this.datasource.columns = [];

    const id = ObjectIdGenerator.generateNewObjectId();
    this.datasource.columns.push(new DBColumn({ id: id, name: this.newColumnNameValue } as IDBColumn));
    this.datasourceLoaded(this.datasource);
    this.newColumnNameValue = '';
    this.newColumnFieldFocused = true;
  };

  onDeleteColumn = (index: number): void => {
    if (!this.datasource || !this.datasource.columns) return;

    this.datasource.columns[index].state = DataState.Deleted;
    this.datasourceLoaded(this.datasource);
  };

  onCoulmnNameInputChange = (value): void => {
    this.newColumnNameValue = value;
  };

  hasColumns = (): boolean => {
    return (
      this.datasource &&
      this.datasource.columns &&
      this.datasource.columns.length > 0 &&
      this.datasource.columns.find((x) => x.state != DataState.Deleted) != null
    );
  };

  onReturn = (value): void => {
    if (value || value.trim() != '') this.onAddColumn();
  };

  ngOnDestroy(): void {
    this.subscriptions.unsubscribe();
  }
}
