import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { AdminDataSourceComponent } from './admin-datasource.component';

const routes: Routes = [
  {
    path: 'create',
    component: AdminDataSourceComponent,
    data: { type: 'create' },
    pathMatch: 'full',
  },
  {
    path: ':datasourceid',
    component: AdminDataSourceComponent,
    data: { type: 'properties' },
    pathMatch: 'full',
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class AdminDataSourceRoutingModule {}
